<template>
  <div v-if="totals" class="orderSum">
    <div class="orderSum__sumsWrapper">
      <div class="orderSum__sums">
        <div class="orderSum__sumsItem">
          <div class="orderSum__sumsItemTitle"> </div>
          <div class="orderSum__sumsItemPrice">
            {{// noinspection JSUnresolvedVariable
            totals.TOTAL_PRODUCTS_PRICE_WITHOUT_DISCOUNT_FORMATTED}}
          </div>
        </div>
        <div v-if="DELIVERY_PRICE_FORMATED" class="orderSum__sumsItem">
          <div class="orderSum__sumsItemTitle"> </div>
          <div class="orderSum__sumsItemPrice">{{DELIVERY_PRICE_FORMATED}}</div>
        </div>
        <!--suppress JSUnresolvedVariable -->
        <div v-if="totals.DISCOUNT_ABSOLUTE_VALUE > 0" class="orderSum__sumsItem">
          <div class="orderSum__sumsItemTitle"></div>
          <div class="orderSum__sumsItemPrice">- {{// noinspection JSUnresolvedVariable
            totals.DISCOUNT_ABSOLUTE_VALUE_FORMATTED}}
          </div>
        </div>
      </div>
      <div data-move="orderSum-total-set"></div>
    </div>
    <div class="orderSum__content">
      <div class="orderSum__promoCode">
        <Coupon v-on:get-basket="getBasket"></Coupon>
      </div>
      <div v-if="bonusesAvailable" class="orderSum__footer">
        <template v-if="USE_BONUSPAY === 'N'">
          <div class="orderSum__bonus">
            <div class="orderSum__bonusTitle"> </div>
            <div class="orderSum__bonussum">{{bonusesAvailableMax}} </div>
          </div>
          <div @click="payWithBonuses" class="orderSum__submit"></div>
        </template>
        <div v-else>    <span class="text-bold">{{bonusesAvailableMax}}</span> </div>
      </div>
      <div v-if="totalPrice" data-move="orderSum-total-get">
        <div class="orderSum__total">
          <div class="orderSum__totalTitle"></div>
          <div class="orderSum__totalPrice">{{Number((totalPrice - BONUS_PAY)).toLocaleString('fr-FR')}} </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState} from "vuex";
  import Coupon from "./Coupon.vue";
  import axios from "axios";

  export default {
    props: ['totals'],
    data() {
      return {
        bonusesAvailable: null
      }
    },
    computed: {
      ...mapState('orderProperties', [
        'DELIVERY_PRICE_FORMATED',
        'TOTAL',
        'ORDER_PRICE_FORMATED',
        'USE_BONUSPAY',
        'BONUS_PAY',
        'USER_ID'
      ]),
      bonusesAvailableMax() {
        return this.bonusesAvailable > this.totalPrice ? this.totalPrice : this.bonusesAvailable
      },
      totalPrice() {
        // noinspection JSUnresolvedVariable
        return this.TOTAL.ORDER_PRICE || this.totals.TOTAL_PRODUCTS_PRICE
      },
      // totalPriceFormatted() {
      //   // noinspection JSUnresolvedVariable
      //   return this.ORDER_PRICE_FORMATED || this.totals.TOTAL_PRODUCTS_PRICE_FORMATTED
      // },
    },
    methods: {
      getBasket() {
        this.$emit('get-basket');
      },
      getBonuses() {
        axios.get('/api/v1/sale/order/bonus/', {
          params: {
            USER_ID: this.USER_ID,
          }
        })
          .then(response => {
            console.log(response);
            if (!response.data.success) throw response.data.errors[0].message;
            this.bonusesAvailable = response.data.data['CURRENT_BUDGET'];
          })
          .catch(error => {
            console.log(error);
          });
      },
      payWithBonuses() {
        this.$store.commit('orderProperties/payWithBonuses', this.bonusesAvailableMax);
      }
    },
    mounted() {
      if (this.USER_ID) {
        this.getBonuses();
      }
    },
    watch: {
      USER_ID() {
        this.getBonuses();
      }
    },
    components: {
      Coupon
    }
  }
</script>
