import iconPoint from '../../images/point.svg'
import iconPointCar from '../../images/point-car.svg'
import {break_sm_up} from "../../js/constants";
import ymaps from '../../../node_modules/ymaps/dist/ymaps.esm.js';

const mapData = {
  icons: {
    iconPoint,
    iconPointCar
  }
};

export function map(id, coordinates) {
  const mapEl = document.getElementById(id);
  if (!coordinates) {
    coordinates.push(+mapEl.dataset.lat, +mapEl.dataset.lng);
  }
  ymaps.load('https://api-maps.yandex.ru/2.1/?apikey=109e4b66-0503-4da1-b84c-bc91501ea5fa&lang=ru_RU&mode=debug').then(maps => {
    const myMap = new maps.Map(id, {
      center: coordinates,
      controls: [],
      zoom: 14
    });

    myMap.controls.add('zoomControl', {
      size: 'small',
      position: {
        right: '20px',
        top: '40px'
      }
    });
    myMap.controls.add('geolocationControl', {
      position: {
        right: '20px',
        top: '110px'
      }
    });

    myMap.behaviors.disable('scrollZoom');

    const iconImageSize = [];
    const iconImageOffset = [];

    if (mapEl.dataset.iconSize) {
      const size = mapEl.dataset.iconSize.split(' ');
      iconImageSize.push(Number(size[0]), Number(size[1]));
      iconImageOffset.push(-Number(size[0]) / 2, -Number(size[0]));
    } else {
      if (window.innerWidth >= break_sm_up) {
        iconImageSize.push(79, 101);
        iconImageOffset.push(-79 / 2, -101);
      } else {
        iconImageSize.push(48, 61);
        iconImageOffset.push(-48 / 2, -61);
      }
    }

    const myPlacemark = new maps.Placemark(myMap.getCenter(), {}, {
      iconLayout: 'default#image',
      iconImageHref: mapData.icons[mapEl.dataset.icon],
      iconImageSize: iconImageSize,
      iconImageOffset: iconImageOffset
    });

    myMap.geoObjects.add(myPlacemark)
  })
}
