<template>
  <div class="b-counter itemSm__counter"><span class="b-counter__minus" v-on:click="count > 0 && count--"></span>
    <div class="b-counter__control">
      <input class="form-control" type="number" v-model="count" data-field="number">
    </div>
    <span class="b-counter__plus" v-on:click="count < 99 && count++"></span>
  </div>
</template>

<script>
  import axios from "axios";

  export default {
    props: {
      id: Number,
      quantity: Number
    },
    data: function () {
      return {
        count: this.quantity
      }
    },
    watch: {
      count: function (value) {
        if(value === 0) {
          this.$emit('delete-item');
        } else {
          this.updateBasketQuantity();
        }
      }
    },
    methods: {
      updateBasketQuantity: function () {
        axios.put(`/api/v1/sale/order/basket/item/${this.id}/?QUANTITY=${this.count}&GET_PRODUCTS_DATA=N`)
          .then(response => {
            console.log(response);
            if (!response.data.success) throw response.data.errors[0].message;

            const {BASKET_TOTALS} = JSON.parse(JSON.stringify(response.data.data));
            this.$emit('update-totals', {BASKET_TOTALS});
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    },
  }
</script>
