<template>
  <div>
    <form v-for="(step, index) in steps" :key="index+1" :id="'cart-order-' +(index+1)" class="cart-order" :class="[{ 'is-loading': step.isLoading, 'is-active': step.isActive, 'is-filled': step.isFilled}, 'cart-order_'+(index+1)]" novalidate>
      <div v-if="!ORDER_ID" class="cart-order__edit" @click="editStep(index+1)"></div>
      <div class="cart-order__title">
        <div class="cart-order__step">{{index+1}}</div>{{step.title}}
        <div v-if="step.id === 4 && deliveryType">&nbsp;({{title}})</div>
      </div>
      <div class="cart-order__content">
        <component :is="componentName(step.isActive, index+1)"
                   :stepIndex="(index+1)"
                   :isFilled="step.isFilled"
        ></component>
      </div>
    </form>
  </div>
</template>

<script>
  import Step1 from "./Step1.vue";
  import Step2 from "./Step2.vue";
  import Step3 from "./Step3.vue";
  import Step4 from "./Step4.vue";
  import Step5 from "./Step5.vue";

  import {mapState} from "vuex";

  export default {
    computed: {
      ...mapState('orderComponents' ,[
        'steps',
        'deliveryVariants',
        'deliveryType'
      ]),
      ...mapState('orderProperties', [
        'ORDER_ID',
      ]),
      title() {
          deliveryTypeOrder = this.deliveryType;

        return this.deliveryVariants[this.deliveryType] && this.deliveryVariants[this.deliveryType].TITLE
      }
    },
    methods: {
      componentName(isActive, index) {
        const name = 'Step'+index;
        return isActive ? name : ''
      },
      editStep(id) {
        this.$store.dispatch('orderComponents/editStep', id);
      }
    },
    components: {
      Step1, Step2, Step3, Step4, Step5
    }
  }
</script>
