<template lang="pug">
  include ../../../node_modules/bemto.pug/bemto
  div
    .productTopSubmit__oneclick(@click="openModal")   1 
    div(v-if="fastOrderData")
      include ../../components/modals/modal-fast-order.pug
      +modal-fast-order([{class: "_single", src: 'fastOrderData.src', title: '{{fastOrderData.title}}', size: '{{fastOrderData.size}}', price: '{{fastOrderData.price}}', priceOld: '{{fastOrderData.priceOld}}'}])
</template>

<script>
import MicroModal from "micromodal/dist/micromodal.min";
import {mapFields} from "vuex-map-fields";
import {registerUser} from "../../js/common/registerUser";

export default {
  data () {
    return {
      fastOrderData: {}
    }
  },
  computed: {
    ...mapFields('orderProperties', [
      'USER_ID',
      'PROPERTY_NAME',
      'PROPERTY_EMAIL',
      'PROPERTY_PERSONAL_PHONE',
    ]),
  },
  methods: {
    getProductData() {
      const $wrapper = document.querySelector('.productTop');
      const $submit = document.querySelector('.js-add-to-basket');
      const $title = document.querySelector('h1');
      const $img = $wrapper.querySelector('.productSliderBig .swiper-slide:first-child img');
      const $size = $wrapper.querySelector('.b-thumb_size.active .b-thumb__size');
      const $price = $wrapper.querySelector('.productTopSum__priceNew');
      const $priceOld = $wrapper.querySelector('.productTopSum__priceOld');
      return {
        id: $submit.dataset.id,
        title: $title.textContent,
        src: $img.src,
        size: $size.textContent,
        price: $price.textContent,
        priceOld: $priceOld && $priceOld.textContent,
      }
    },
    events() {
      this.form.addEventListener('bouncerFormValid', () => {
        if (this.USER_ID) {
          this.$store.dispatch('orderProperties/orderSaveFast', this.fastOrderData.id);
        } else {
          registerUser(this.form, 'hidden');
        }
      });
      this.form.addEventListener('user-hidden-register', e => {
        this.$store.commit('setState', {
          module: 'orderProperties',
          state: 'USER_ID',
          value: e.detail.USER_ID
        });
        this.$store.dispatch('orderProperties/orderSaveFast', this.fastOrderData.id);
      });
    },
    openModal() {
      this.fastOrderData = this.getProductData();
      MicroModal.show('modal-fast-order');
    }
  },
  mounted() {
    this.form = this.$el.querySelector('#form-fast-order');
    this.$store.dispatch('orderProperties/getUserSession');
    this.events();
  }
}
</script>
