<template>
  <div>
    <div class="cart-order-grid">
      <div class="cart-order-grid__item">
        <div class="form-group">
          <label for="cart-1-checkbox-name"> </label>
          <!--suppress JSUnresolvedVariable -->
          <input id="cart-1-checkbox-name" v-model="PROPERTY_NAME" name="NAME" class="form-control" type="text" placeholder=" " required>
        </div>
        <div class="form-group">
          <label for="cart-1-checkbox-email">E-mail</label>
          <!--suppress JSUnresolvedVariable -->
          <input id="cart-1-checkbox-email" v-model="PROPERTY_EMAIL" :disabled="USER_ID" name="EMAIL" class="form-control" type="email" placeholder=" " required>
        </div>
        <div class="form-group form-group_lg">
          <label for="cart-1-checkbox-tel"> </label>
          <!--suppress JSUnresolvedVariable -->
          <input id="cart-1-checkbox-tel" v-model="PROPERTY_PERSONAL_PHONE" name="PERSONAL_PHONE" class="form-control" type="tel" data-field="phone" placeholder="  " minlength="11" required>
        </div>
      </div>
      <div class="cart-order-grid__item cart-order-grid__item_swap">
        <p>    .      (   )</p>
      </div>
    </div>
    <div class="cart-order-grid">
      <div class="cart-order-grid__item" style="width: 100%">
        <div class="form-group form-group_lg">
          <input v-model="isSendPhoto" class="checkbox" type="checkbox" id="cart-1-checkbox-2" data-toggle="checkbox-toggle" data-target="#send-photo" @click="updateSendTo()">
          <label for="cart-1-checkbox-2">   WhatsApp   (       WhatsApp)</label>
        </div>
      </div>
    </div>
    <div class="cart-order-grid">
      <div class="cart-order-grid__item">
        <button type="submit" class="button button-dark button-block button-submit"></button>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapState} from 'vuex';
  import { mapFields } from 'vuex-map-fields';
  import validate from "../../mixins/validate";
  import orderProperties from "../../store/modules/orderProperties";
  import orderComponents from "../../store/modules/orderComponents";
  import {phoneMask} from "../../../js/phone-mask";
  import {registerUser} from "../../../js/common/registerUser";

  export default {
    mixins: [validate],
    props: [
      'stepIndex'
    ],
    data () {
      return {
        isSendPhoto: false,
      }
    },
    computed: {
      ...mapState('orderProperties', [
        'PROPERTY_SEND_PHOTO'
      ]),
      ...mapFields('orderProperties', [
        'PROPERTY_NAME',
        'PROPERTY_EMAIL',
        'PROPERTY_PERSONAL_PHONE',
        'PROPERTY_NOT_CALL_CONFIRM',
        'USER_ID'
      ]),
      ...mapGetters('orderComponents', [
        'getStepById'
      ])
    },
      methods: {
          updateSendTo() {
              let value = this.isSendPhoto ? [] : 'whatsapp';

              this.$store.commit('setState', {
                  module: 'orderProperties',
                  state: 'PROPERTY_SEND_PHOTO',
                  value: value
              });
          },
      },
    mounted() {
      phoneMask(this.$el.querySelector('[data-field="phone"]'));
      this.$store.dispatch('orderProperties/getUserSession');
      this.form.addEventListener('bouncerFormValid', () => {
        if (this.USER_ID) {
          this.$store.dispatch('orderComponents/nextStepAction');
        } else {
          registerUser(this.form, 'hidden');
        }
      });
      this.form.addEventListener('user-hidden-register', e => {
        this.$store.commit('setState', {
          module: 'orderProperties',
          state: 'USER_ID',
          value: e.detail.USER_ID
        });
        this.$store.dispatch('orderComponents/nextStepAction');
      });
    }
  }
</script>
