import _ from "lodash";

export default {
  namespaced: true,
  state: {
    BASKET_ITEMS: [],
    BASKET_TOTALS: {},
    littleThingsProducts: []
  },
  mutations: {
    setLittleThingsProducts(state, payload) {
      state.littleThingsProducts = payload
    },
    removeItem(state, payload) {
      const items = JSON.parse(JSON.stringify(state.BASKET_ITEMS));
      _.remove(items, item => item.ID === payload);
      state.BASKET_ITEMS = items;
    }
  },
  actions: {
    getLittleThings({commit}) {
      axios.get('/api/v1/catalog/products/from_order/')
        .then(response => {
          if (!response.data.success) throw response.data.errors[0].message;
          commit('setLittleThingsProducts', Object.values(response.data.data["products"]));
        })
        .catch(error => {
          alert(error);
        });
    },
  }
}
