import Vue from 'vue'
import Vuex from 'vuex'
import orderProperties from "./modules/orderProperties";
import orderComponents from "./modules/orderComponents";
import basket from "./modules/basket";
import axios from "axios";
window.axios = axios;

Vue.use(Vuex);

export default new Vuex.Store({
  mutations: {
    setState(state, payload) {
      if (payload.module) {
        state[payload.module][payload.state] = payload.value;
      } else {
        state[payload.state] = payload.value;
      }
    }
  },
  modules: {
    orderComponents,
    orderProperties,
    basket
  }
});