import timedelivery from "../data/timedelivery";
import timedelivery_special from "../data/timedelivery_special";
import timedelivery_pickup from "../data/timedelivery_pickup";
import moment from 'moment';

export function getActualIntervals(deliveryFromMoment, deliveryChosenDateMoment) {
  deliveryChosenDateMoment = deliveryChosenDateMoment || deliveryFromMoment;

  let timedeliveryUsed = null;

  let specialDates = getSpecialDates();

  let deliveryType = document.querySelector('input[name=delivery]:checked');

  let specialDateVarn = document.getElementById('special-date-varn');

  let isSpecialDate = specialDates.includes(deliveryChosenDateMoment.format('YYYY-MM-DD'));

  if (isSpecialDate && specialDateVarn)
    specialDateVarn.style.display = "block";
  else if (specialDateVarn)
    specialDateVarn.style.display = "none";

  if (deliveryType && deliveryType.value === 'pvz')
    timedeliveryUsed = timedelivery_pickup;
  else if (isSpecialDate)
    timedeliveryUsed = timedelivery_special;
  else
    timedeliveryUsed = timedelivery;

  const data = JSON.parse(JSON.stringify(timedeliveryUsed));

  //31     
  if (deliveryChosenDateMoment.format('MM-DD') === '12-31' && typeof data.INTERVALS != "undefined")
    data.INTERVALS[0].values.pop();

  //    
  if (!deliveryType || deliveryType.value !== 'pvz') {
    for (let obj in data) {
      if (obj === 'EXACT_TIME')
        continue;

      // debugger
      _.remove(data[obj], interval => {
        _.remove(interval.values, value => {
          let time;
          if (obj === 'EXACT_TIME') {
            time = value[0].split(':');
          } else {
            time = value[0].split('-')[0].split(':');
          }
          return deliveryFromMoment.isAfter(moment(deliveryChosenDateMoment).hours(time[0]).minutes(time[1]))
        });
        return interval.values.length === 0
      });
    }
  }

  return data;
}

export function getFirstInterval(deliveryFromMoment) {
  const actualIntervals = getActualIntervals(deliveryFromMoment);

  return actualIntervals.INTERVALS[0].values[0][0];
}

/**
 *      ?
 *
 * @param interval
 * @param deliveryFromMoment
 * @returns {*|boolean}
 */
export function isIntervalIsAfterDeliverTime(interval, deliveryFromMoment) {
  let time = interval[0].split('-')[1].split(':');

  return deliveryFromMoment.isAfter(moment().hours(time[0]).minutes(time[1]));
}

/**
 *     
 *
 * @returns {*}
 */
export function getCheapestInterval(deliveryFromMoment) {
  const intervals = getActualIntervals(deliveryFromMoment).INTERVALS;

  for (let i = 0; i < intervals.length; i++) {
    let interval = intervals[i];

    for (let z = 0; z < interval.values.length; z++) {
      let intervalData = interval.values[z];

      let intervalPrice = intervalData[1];

      if (!isIssetSmallerIntervalPrice(intervalPrice, intervals))
        return intervalData;
    }
  }
}

/**
 *         
 *
 * @param price
 * @param intervals
 * @returns {boolean}
 */
export function isIssetSmallerIntervalPrice(price, intervals) {
  for (let i = 0; i < intervals.length; i++) {
    let interval = intervals[i];

    for (let z = 0; z < interval.values.length; z++) {
      let intervalData = interval.values[z];

      let intervalPrice = intervalData[1];

      if (intervalPrice < price)
        return true;
    }
  }

  return false;
}

/**
 *     
 *
 * @returns {*[]}
 */
export function getSpecialDates() {
  let year = new Date().getFullYear();

  let lastSundayOfNovember = getLastSundayOfNovember(year);

  return [
    year + "-02-14",

    year + "-03-04",
    year + "-03-05",
    year + "-03-06",
    year + "-03-07",
    year + "-03-08",
    year + "-03-09",

    year + "-11-" + lastSundayOfNovember.getDate(),

    year + "-12-28",
    year + "-12-29",
    year + "-12-30",
    year + "-12-31",
  ];
}

/**
 *    
 *
 * @param year
 * @returns {Date}
 */
export function getLastSundayOfNovember(year) {
  let day = 31;

  let isLastSunday = null;

  do {
    day--;

    isLastSunday = new Date('November ' + day + ', ' + year);
  } while (isLastSunday.getDay() !== 0);

  return isLastSunday;
}
