<template>
  <div class="order-grid__side" v-if="BASKET_ITEMS.length">
    <div class="order-title"> </div>
    <basket-list v-bind:BASKET_ITEMS="BASKET_ITEMS" v-on:update-items="updateItems" v-on:update-totals="updateTotals"></basket-list>
    <div class="orderSticky">
      <div data-move="orderSum-get">
        <basket-sum v-bind:totals="BASKET_TOTALS" v-on:get-basket="getBasket"></basket-sum>
      </div>
      <Little-thing v-on:get-basket="getBasket" />
    </div>
  </div>
</template>

<script>
  import {mapState} from "vuex";
  import axios from "axios";
  import BasketList from "./BasketList.vue";
  import BasketSum from "./BasketSum.vue";
  import LittleThing from "./Little-thing.vue";

  export default {
    computed: {
      ...mapState('basket', [
        'BASKET_ITEMS',
        'BASKET_TOTALS'
      ]),
    },
    methods: {
      updateItems: function (data) {
        this.$store.commit('setState', {
          module: 'basket',
          state: 'BASKET_ITEMS',
          value: data.BASKET_ITEMS
        });
      },
      updateTotals: function (data) {
        console.log(data.BASKET_TOTALS);
        this.$store.dispatch('orderProperties/orderCalculate');
        this.$store.commit('setState', {
          module: 'basket',
          state: 'BASKET_TOTALS',
          value: data.BASKET_TOTALS
        });
      },
      getBasket(GET_PRODUCTS_DATA = 'Y') {
        axios.get('/api/v1/sale/order/basket/', {
          params: {
            GET_PRODUCTS_DATA
          }
        })
          .then(response => {
            console.log(response);
            if (!response.data.success) throw response.data.errors[0].message;

            const data = JSON.parse(JSON.stringify(response.data.data));
            if(GET_PRODUCTS_DATA === 'Y') {
              this.$store.commit('setState', {
                module: 'basket',
                state: 'BASKET_ITEMS',
                value: data.BASKET_ITEMS
              });


                /*region dataLayer */
                let products = [];
                data.BASKET_ITEMS.forEach(function(item) {
                    let category = typeof item.PRODUCT_DATA.sections_text_chain !== 'undefined' ? item.PRODUCT_DATA.sections_text_chain.split("/")[0] : false;

                    products.push({
                        name: item.PRODUCT_DATA.name,
                        id: item.PRODUCT_DATA.id,
                        price: item.PRICE,
                        category: category,
                        quantity: item.QUANTITY
                    });
                });

                if (typeof dataLayer !== 'undefined' && products.length){
                    if(typeof dataLayerProducts !== 'undefined') {
                        dataLayerProducts = products;

                        localStorage.setItem("dataLayerProducts", JSON.stringify(dataLayerProducts));
                    }

                    dataLayer.push({ ecommerce: null });

                    dataLayer.push({
                        event: 'checkout',
                        ecommerce: {
                            currencyCode: 'RUB',
                            checkout: {
                                actionField: {'step': 1, 'action': 'cart'},
                                products: products
                            }
                        }
                    });
                }
                /*endregion dataLayer*/
            }
            this.$store.commit('setState', {
              module: 'basket',
              state: 'BASKET_TOTALS',
              value: data.BASKET_TOTALS
            });
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    },
    mounted() {
      this.getBasket('Y');
    },
    components: {
      BasketList,
      BasketSum,
      LittleThing
    }
  }

</script>
