import moment from 'moment';
import 'moment/locale/ru';

moment.locale('ru');
import axios from "axios";
import {getField, updateField} from "vuex-map-fields";
import {getActualIntervals} from "../../functions/delivery";
import {getCheapestInterval} from "../../functions/delivery";

const req = new XMLHttpRequest();
req.open("GET", '/ajax/date.php', false);
req.send();

let deliveryFromMoment = moment(req.response).add(3, 'h');

let intervals = getActualIntervals(deliveryFromMoment);

if (!intervals.INTERVALS.length)
  deliveryFromMoment = moment(deliveryFromMoment).add(1, 'd').hours(0).minutes(0).seconds(0);

let cheapestInterval = getCheapestInterval(deliveryFromMoment);

const PROPERTY_DELIVERY_INTERVAL = cheapestInterval[0];

const locationSelect = document.querySelector('#popover-header .select-default .select');

export default {
  namespaced: true,
  state: {
    USER_ID: null,
    PERSON_TYPE_ID: 1,
    PROPERTY_NAME: '',
    PROPERTY_EMAIL: '',
    PROPERTY_PERSONAL_PHONE: '',
    PROPERTY_NOT_CALL_CONFIRM: 'N',
    PROPERTY_SEND_PHOTO: [],

    PROPERTY_RECIPIENT_NAME: '',
    PROPERTY_RECIPIENT_PHONE: '',
    PROPERTY_RECIPIENT_MAKE_PHOTO: 'N',
    PROPERTY_IS_SURPRISE: 'N',
    PROPERTY_ANONYMOUS_ORDER: 'N',
    PROPERTY_NEED_POSTCARD: 'N',
    PROPERTY_POSTCARD: '',

    LOCATION: locationSelect.value,
    DELIVERY_ID: null,
    PROPERTY_GET_ADDRESS: 'N',
    PROPERTY_CITY: locationSelect.options[locationSelect.selectedIndex].text,
    PROPERTY_STREET: '',
    PROPERTY_HOME: '',
    PROPERTY_APARTMENT: '',
    USER_DESCRIPTION: '',
    PROPERTY_DELIVERY_COMMENT: '',
    PROPERTY_DISTANCE: null,
    PROPERTY_ADDRESS_COORDINATES: '',
    PROPERTY_DELIVERY_DATE: deliveryFromMoment.format('DD.MM.YYYY'),
    PROPERTY_DELIVERY_INTERVAL: PROPERTY_DELIVERY_INTERVAL,
    PROPERTY_EXACT_TIME: '',
    PAY_SYSTEM_ID: 3,
    PROPERTY_COMPANY_NAME: '',
    FILE_BILL: null,

    USE_BONUSPAY: 'N',
    BONUS_PAY: null,

    ORDER_PRICE_FORMATED: '',
    DELIVERY_PRICE_FORMATED: '',
    ORDER_ID: null,
    ORDER_PRODUCTS: [],
    TOTAL: {},
    expressDelivery: false,
    deliveryFromMoment,
    deliveryChosenDateMoment: deliveryFromMoment
  },
  getters: {
    timeIntervalActual: (state) => getActualIntervals(deliveryFromMoment, state.deliveryChosenDateMoment),
    getField
  },
  mutations: {
    setUserData(state, payload) {
      state.USER_ID = payload['ID'];
      state.PROPERTY_NAME = payload['NAME'];
      state.PROPERTY_EMAIL = payload['EMAIL'];
      state.PROPERTY_PERSONAL_PHONE = payload['PERSONAL_PHONE'];
    },
    updatePrices(state, payload) {
      state.TOTAL = payload;
      state.DELIVERY_PRICE_FORMATED = payload.DELIVERY_PRICE_FORMATED;
      state.ORDER_PRICE_FORMATED = payload.ORDER_PRICE_FORMATED;
    },
    updateAddress(state, payload) {
      state.PROPERTY_CITY = payload.city;
      state.PROPERTY_DISTANCE = payload.distance;
      state.PROPERTY_STREET = payload.street;
      state.PROPERTY_HOME = payload.home;
    },
    clearAddress(state) {
      state.PROPERTY_STREET = '';
      state.PROPERTY_HOME = '';
      state.PROPERTY_APARTMENT = '';
    },
    clearCompanyData(state) {
      state.PROPERTY_COMPANY_NAME = '';
      state.FILE_BILL = null;
    },
    payWithBonuses(state, payload) {
      state.USE_BONUSPAY = 'Y';
      state.BONUS_PAY = payload;
    },
    SET_deliveryChosenDateMoment(state, payload) {
      state.deliveryChosenDateMoment = payload
    },
    SET_PROPERTY_DELIVERY_DATE(state, payload) {
      state.PROPERTY_DELIVERY_DATE = payload
    },
    updateField
  },
  actions: {
    SET_date({state, commit}, date) {
      commit('SET_deliveryChosenDateMoment', date.hours(state.deliveryChosenDateMoment.hours()).minutes(state.deliveryChosenDateMoment.minutes()));
      if (moment(state.deliveryChosenDateMoment).hours(state.PROPERTY_DELIVERY_INTERVAL.split(' - ')[0].split(':')[0]).isAfter(deliveryFromMoment)) {
        commit('SET_PROPERTY_DELIVERY_DATE', date.format('DD.MM.YYYY'));
      }
    },
    getUserSession({commit}) {
      axios.get('/api/v1/user-session/')
        .then(response => {
          console.log(response);
          if (!response.data.success) throw response.data.errors[0].message;
          commit('setUserData', response.data.data['USER']);
        })
        .catch(error => {
          console.log(error);
        });
    },
    orderCalculate({state, commit, rootState}) {
      if (!state.DELIVERY_ID)
        return;

      let timerId =  setTimeout(function() {
        clearTimeout(timerId);

        let expressDelivery = document.querySelector('#express-delivery');

        rootState.orderProperties.expressDelivery = !!(expressDelivery && expressDelivery.checked);

        axios.post('/api/v1/sale/order/calculate/', Object.assign({}, rootState.orderProperties))
          .then(response => {
            if (!response.data.success)
              throw response.data.errors[0].message;

            commit('updatePrices', response.data.data.TOTAL);
          })
          .catch(error => {
            console.log(error);
          });
      }, 1000);
    },
    orderSave({state, commit, rootState, rootGetters}) {
      const currentStep = rootGetters['orderComponents/getStepById'](rootGetters['orderComponents/stepCurrentIndex']);
      commit('orderComponents/loading', {currentStep}, {root: true});
      axios.post('/api/v1/sale/order/save/', Object.assign({}, rootState.orderProperties))
        .then(response => {
          if (!response.data.success) throw response.data.errors[0].message;
          let responseData = response.data.data;

          state.ORDER_PRODUCTS = responseData.ORDER_PRODUCTS;

          let content_ids = [], content_name = '', content_category = '';
          state.ORDER_PRODUCTS.forEach(function (product) {
            content_ids.push(product.PRODUCT_ID);
            content_name += product.NAME + ' / ';
            if (product.NAV_CHAIN)
              content_category += product.NAV_CHAIN + ', ';
          });

          if (typeof fbq !== 'undefined') {
            fbq('track', 'InitiateCheckout', {
              content_ids: content_ids,
              content_name: content_name,
              content_category: content_category,
              value: responseData.ORDER_PRICE,
              content_type: 'product',
              currency: 'RUB',
            });
          }

          commit('setState', {
            module: 'orderProperties',
            state: 'ORDER_ID',
            value: responseData.ORDER_ID
          }, {root: true});
          commit('orderComponents/loading', {currentStep, loading: false}, {root: true});
        })
        .catch(error => {
          console.log(error);
        });
    },
    orderSaveFast({state, commit, dispatch}, id) {
      if (typeof ym !== 'undefined')
        ym(71116915, 'reachGoal', '1click');

      axios.post('/api/v1/sale/order/saveFast/', {
        LOCATION: state.LOCATION,
        USER_ID: state.USER_ID,
        PERSON_TYPE_ID: state.PERSON_TYPE_ID,
        PROPERTY_NAME: state.PROPERTY_NAME,
        PROPERTY_EMAIL: state.PROPERTY_EMAIL,
        PROPERTY_PERSONAL_PHONE: state.PROPERTY_PERSONAL_PHONE,
        PRODUCT_ID: id,
      })
        .then(response => {
          if (!response.data.success) throw response.data.errors[0].message;

          _tmr.push({id: 3208143, type: "reachGoal", goal: "lead", value: response.data.data.ORDER_PRICE});
          _tmr.push({
            type: 'itemView',
            productid: id,
            pagetype: 'lead',
            list: 1,
            totalvalue: response.data.data.ORDER_PRICE
          });

          commit('setState', {
            module: 'orderProperties',
            state: 'ORDER_ID',
            value: response.data.data.ORDER_ID
          }, {root: true});

          dispatch('goToSuccessPage');
        })
        .catch(error => {
          console.log(error);
        });
    },
    goToSuccessPage({state}) {
      location.href = location.origin + '/personal/order/make/?ORDER_ID=' + state.ORDER_ID
    },
  }
}
