import axios from "axios";
import _ from 'lodash'
import {getField, updateField} from "vuex-map-fields";

export default {
  namespaced: true,
  state: {
    steps: [
      {
        id: 1,
        title: ' ',
        isActive: true,
        isFilled: false,
        isLoading: false
      },
      {
        id: 2,
        title: '',
        isActive: false,
        isFilled: false,
        isLoading: false
      },
      {
        id: 3,
        title: '',
        isActive: false,
        isFilled: false,
        isLoading: false
      },
      {
        id: 4,
        title: ' ',
        isActive: false,
        isFilled: false,
        isLoading: false
      },
      {
        id: 5,
        title: '',
        isActive: false,
        isFilled: false,
        isLoading: false
      }
    ],
    deliveryVariants: {},
    deliveryType: undefined
  },
  getters: {
    getStepById: state => id => {
      return state.steps.find(step => step.id === id);
    },
    stepCurrentIndex: state => {
      return _.last(state.steps.filter(step => step.isActive === true && step.isFilled === false)).id
    },
    getField
  },
  mutations: {
    loading(state, {currentStep, loading = true}) {
      currentStep.isLoading = loading;
    },
    loadNextStep(state, {currentStep, nextStep}) {
      currentStep.isLoading = false;
      currentStep.isFilled = true;
      nextStep.isActive = true;
    },
    editStep(state, {currentStep, toEditStep}) {
      currentStep.isActive = false;
      toEditStep.isFilled = false;
      toEditStep.isActive = true;
    },
    updateField
  },
  actions: {
    editStep({commit, state, getters}, id) {
      const currentStep = getters.getStepById(getters.stepCurrentIndex);
      const toEditStep = getters.getStepById(id);
      commit('editStep', {currentStep, toEditStep})
    },
    nextStepAction({state, commit, getters}) {
      const currentStep = getters.getStepById(getters.stepCurrentIndex);
      const lastFilled = _.last(state.steps.filter(step => step.isFilled === true));
      let nextStep = getters.getStepById(getters.stepCurrentIndex + 1);
      if (lastFilled && lastFilled.id > getters.stepCurrentIndex) {
        nextStep = getters.getStepById(lastFilled.id + 1)
      }
      commit('loading', {currentStep});
      setTimeout(() => {
        commit('loadNextStep', {currentStep, nextStep})
      }, 500);
    },
    getAvailableDelivery({commit, rootState}) {
      axios.get('/api/v1/sale/order/delivery/', {
        params: {
          LOCATION: rootState.orderProperties.LOCATION,
          PERSON_TYPE_ID: rootState.orderProperties.PERSON_TYPE_ID,
        }
      })
        .then(response => {
          if (!response.data.success) throw response.data.errors[0].message;
          console.log(response.data.data);
          commit('setState', {
            module: 'orderComponents',
            state: 'deliveryVariants',
            value: response.data.data
          }, {root: true});
          commit('setState', {
            module: 'orderComponents',
            state: 'deliveryType',
            value: Object.keys(response.data.data)[0]
          }, {root: true});
        })
        .catch(error => {
          alert(error);
        });
    }
  }
}
