<template>
  <div>
    <div class="order-time">
      <div class="form-group">
        <div class="big-dates">
          <div class="big-dates__list">
            <div v-for="(item, index) in listGroupSiblingsDates" :key="index" @click.prevent="dateClicked(item, index)" :class="{ active: item.date === PROPERTY_DELIVERY_DATE}" class="big-dates__item big-dates__item_date">
              <div class="big-dates__weekday">{{item.shorts[1]}}</div>
              <div class="big-dates__day">{{item.shorts[0]}} {{item.shorts[2]}}</div>
            </div>
            <select v-model="selected_DATE" class="form-control">
              <option v-for="(item, index) in listGroupSiblingsDates" :key="index" :value="item.date">{{item.shorts[1]}} {{item.shorts[0]}} {{item.shorts[2]}}</option>
              <option v-if="dateIsOut" :value="PROPERTY_DELIVERY_DATE">{{date_dd_MMM_DD}}</option>
            </select>
            <div id="datepickerOrder" class="big-dates__item big-dates__item_calendar">
              <input @change="datepickerChanged" type="hidden">
              <svg>
                <use xlink:href="#calend"></use>
              </svg>
            </div>
          </div>
        </div>
      </div>

      <div class="form-group form-group_lg">
        <div v-if="deliveryType === 'courier'" class="pane pane_time time-price-list" id="express-delivery-block">
          <div class="pane__header express_delivery">
            <div class="time-price-list__title">
              <span>
                <label style="margin: 0">
                  <input type="radio" @change="onChangePickupTime" name="express-delivery" id="express-delivery">
                   ,   <span id="express-delivery-visual"></span>
                  </label>
              </span>
              <span>490 </span>
            </div>
          </div>
          <div class="pane__content" style="display: none"></div>
        </div>

        <div id="special-date-varn" class="time-price-list" style="text-align: right; color: red; display: none;">      </div>

        <template v-for="(intervals, key) in timeIntervalActual">
          <div v-for="(item, index) in intervals" :key="key+index" class="pane pane_time time-price-list" v-bind:class="{ active: item.type === 'pickup' }">
            <div v-if="item.type === 'pickup'" id="pickup-info" class="time-price-list" style="text-align: center; color: red; font-style: italic; font-family: Verdana, Geneva, sans-serif; font-size: 11px; margin-top: 5px;">     ,     </div>
            <div id="exact-time-varn" class="time-price-list" style="text-align: center; color: red; display: none;">      3   </div>
            <div id="exact-time-varn-31-December" class="time-price-list" style="text-align: center; color: red; display: none;">31-     20:00</div>

            <div class="pane__header">
              <div class="time-price-list__title"><span>{{item.interval}}</span><span>{{item.from}}</span></div>
            </div>
            <div class="pane__content">
              <div class="pane__contentInner" v-bind:style="{ paddingRight: item.type === 'deliveryToExactTime' ? 0 : '' }">
                <div v-for="(time, index2) in item.values" :key="key+index2" class="form-group form-group_sm time-price" v-bind:style="{ flexWrap: item.type === 'deliveryToExactTime' ? 'wrap' : '', maxWidth: item.type === 'deliveryToExactTime' ? '100%' : '' }">
                  <template v-if="item.type === 'pickup'">
                    <!--suppress JSUnresolvedVariable, HtmlFormInputWithoutLabel -->
                    <input @change="onChangePickupTime" type="time" class="form-control" :id="`cart-4-radio-time-${key}-${index}-${index2}`" name="cart-4-radio-time" placeholder="10:00" required style="height: 35px; width: 50%;" maxlength="5">
                    <label :for="`cart-4-radio-time-${key}-${index}-${index2}`" style="padding-top: 5px; color: gray;">: 10:00</label>
                  </template>
                  <template v-else-if="item.type === 'deliveryToExactTime'">
                    <!--suppress JSUnresolvedVariable, HtmlFormInputWithoutLabel -->
                    <input @change="onChangePickupTime" type="time" class="form-control" :id="`cart-4-radio-time-${key}-${index}-${index2}`" name="cart-4-radio-time" placeholder="10:00" style="height: 35px; width: 50%;" maxlength="5">
                    <label :for="`cart-4-radio-time-${key}-${index}-${index2}`" style="padding-top: 5px; color: gray; padding-right: 10px;">: 10:00</label>
                    <div id="deliveryToExactTime-info" class="time-price-list" style="width: 100%; text-align: right; color: gray; font-style: italic; font-family: Verdana, Geneva, sans-serif; font-size: 11px; margin-top: 10px; padding-right: 10px;">   +/- 15    </div>
                  </template>
                  <template v-else>
                    <!--suppress JSUnresolvedVariable, HtmlFormInputWithoutLabel -->
                    <input v-model="PROPERTY_DELIVERY_INTERVAL" :value="time[0]" class="checkbox" type="radio" :id="`cart-4-radio-time-${key}-${index}-${index2}`" name="cart-4-radio-time" data-bouncer-target="#checkbox-error">
                    <label :for="`cart-4-radio-time-${key}-${index}-${index2}`">{{time[0]}}</label><span>{{time[1]}}</span>
                  </template>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
    <div class="cart-order-grid">
      <div class="cart-order-grid__item">
        <button type="submit" class="button button-dark button-block button-submit"></button>
      </div>
      <div class="cart-order-grid__item cart-order-grid__item_swap cart-order-grid__item_filled-swap">
        <!--suppress JSUnresolvedVariable -->
        <div v-if="PROPERTY_DELIVERY_INTERVAL" class="cart-order-time">{{date_dd_mm_YYY_INTERVAL}}</div>
      </div>
    </div>
  </div>
</template>

<script>
    import {forEach} from "../../../js/helpers";
    import moment from 'moment';
    import 'moment/locale/ru';
    import {getActualIntervals} from "../../functions/delivery";

    moment.locale('ru');
    import {datepicker} from "../../../components/datepicker/datepicker";
    import {pane} from "../../../components/pane/pane";
    import {mapState} from "vuex";
    import {mapGetters} from 'vuex';
    import {mapFields} from 'vuex-map-fields';
    import validate from "../../mixins/validate";

    export default {
        mixins: [validate],
        props: [
            'stepIndex',
            'isFilled'
        ],
        data() {
            return {
                selected_DATE: null
            }
        },
        computed: {
            ...mapState('orderProperties', [
                'PROPERTY_DELIVERY_DATE',
                'deliveryFromMoment',
                'deliveryChosenDateMoment'
            ]),
            ...mapState('orderComponents', [
                'deliveryVariants',
                'deliveryType'
            ]),
            ...mapGetters('orderProperties', [
                'timeIntervalActual',
            ]),
            ...mapFields('orderProperties', [
                'PROPERTY_DELIVERY_DATE',
                'PROPERTY_DELIVERY_INTERVAL'
            ]),
            ...mapFields('orderComponents', [
                'deliveryType'
            ]),
            dateIsOut() {
                return moment(this.PROPERTY_DELIVERY_DATE, 'DD.MM.YYYY').isAfter(moment(this.deliveryFromMoment).add(3, 'days'))
            },
            listGroupSiblingsDates() {
                const arr = [];
                for (let i = 0; i < 4; i++) {
                    const obj = {};
                    const date = moment().add(i, 'days');
                    obj.date = date.format('DD.MM.YYYY');
                    obj.shorts = [];
                    obj.shorts = date.format("MMM dd DD").split(' ');
                    arr.push(obj);
                }
                return arr;
            },
            date_dd_MMM_DD() {
                return moment(this.PROPERTY_DELIVERY_DATE, 'DD.MM.YYYY').format("dd MMM DD")
            },
            date_dd_mm_YYY_INTERVAL() {
                return `${moment(this.PROPERTY_DELIVERY_DATE, 'DD.MM.YYYY').format('D MMMM YYYY')}, ${this.PROPERTY_DELIVERY_INTERVAL}`
            },
        },
        methods: {
            dateClicked(item) {
                let expressDelivery = document.querySelector('#express-delivery');

                if (expressDelivery && expressDelivery.checked) {
                    this.selected_DATE = this.PROPERTY_DELIVERY_DATE = this.selectedDateDefault;
                    this.PROPERTY_DELIVERY_INTERVAL = this.intervalDefault;

                    expressDelivery.checked = false;
                }

                let intervals = getActualIntervals(moment(item.date, 'DD.MM.YYYY'));

                if (typeof intervals.INTERVALS != "undefined") {
                    intervals = intervals.INTERVALS[0].values;

                    if (intervals && intervals !== "undefined") {
                        let intervalExist = false;

                        for (let interval of intervals) {
                            if (interval[0] === this.PROPERTY_DELIVERY_INTERVAL) {
                                intervalExist = true;
                                break;
                            }
                        }

                        if (!intervalExist)
                            this.PROPERTY_DELIVERY_INTERVAL = intervals.pop()[0];
                    }
                }

                this.$store.dispatch('orderProperties/SET_date', moment(item.date, 'DD.MM.YYYY'));
            },
            datepickerChanged(e) {
                this.$store.dispatch('orderProperties/SET_date', moment(new Date(e.target.value)));
            },
            onChangePickupTime(elm) {
                this.PROPERTY_DELIVERY_INTERVAL = elm.target.value;
            },
        },
        watch: {
            selected_DATE(value) {
                this.$store.dispatch('orderProperties/SET_date', moment(value, 'DD.MM.YYYY'));
            },
            PROPERTY_DELIVERY_DATE(value) {
                this.selected_DATE = value
            }
        },
        mounted() {
            this.selectedDateDefault = this.selected_DATE = this.PROPERTY_DELIVERY_DATE;
            this.intervalDefault = this.PROPERTY_DELIVERY_INTERVAL;

            datepicker(this.$el.querySelector('#datepickerOrder'));
            forEach(this.$el.querySelectorAll('.pane'), pane);

            let year = new Date().getFullYear();

            let expressDelivery = document.querySelector('#express-delivery');

            if (expressDelivery) {
                jQuery('input[name=cart-4-radio-time]').on("change", function () {
                    expressDelivery.checked = false;
                });

                const req = new XMLHttpRequest();
                req.open("GET", '/ajax/date.php', false);
                req.send();

                let date = moment(req.response);

                //31      15:30
                let maxTimeDecember31ExpressDelivery = moment(year + '-12-31 15:30:00');

                if (date.isAfter(maxTimeDecember31ExpressDelivery))
                    document.querySelector('#express-delivery-block').style.display = "none";

                jQuery(expressDelivery).on("change", params => {
                    if (expressDelivery.checked)
                        this.selected_DATE = this.PROPERTY_DELIVERY_DATE = this.deliveryFromMoment.format('DD.MM.YYYY');
                    else {
                        this.selected_DATE = this.PROPERTY_DELIVERY_DATE = this.selectedDateDefault;
                        this.PROPERTY_DELIVERY_INTERVAL = this.intervalDefault;
                    }
                });

                //31    4.5 ,    2.5 
                let expressDeliveryTo = (date.format('DD.MM') === '31.12') ? date.add(4, 'h').add(30, 'm') : date.add(2, 'h').add(30, 'm');

                let ceilMinutes = Math.ceil(expressDeliveryTo.format("m") / 10) * 10;

                expressDeliveryTo = expressDeliveryTo.minutes(ceilMinutes).format("HH:mm");

                document.querySelector('#express-delivery-visual').textContent = expressDelivery.value = expressDeliveryTo;
            }

            this.form.addEventListener('bouncerFormValid', () => {
                let varn = document.querySelector('#exact-time-varn');

                let varn_December31 = document.querySelector('#exact-time-varn-31-December');

                varn.style.display = "none";

                varn_December31.style.display = "none";

                let time = document.querySelector('.cart-order-time').textContent;

                time = time.substring(time.indexOf(',') + 1).trim().match(/^\d+:\d+$/);

                time = (time !== null) ? time[0] : null;

                let selectedDateTime = null;

                if (time) {
                    selectedDateTime = this.PROPERTY_DELIVERY_DATE + ' ' + time;

                    selectedDateTime = moment(selectedDateTime, 'DD.MM.YYYY HH:mm');
                }

                if (expressDelivery && time && selectedDateTime && !expressDelivery.checked) {
                    if (this.deliveryFromMoment.isAfter(selectedDateTime)) {
                        varn.style.display = "block";

                        return false;
                    }
                }

                //   31,  20:00
                if (deliveryTypeOrder === 'pvz' && time && selectedDateTime) {
                    let december31 = moment(year + '-12-31 20:00:00');

                    if (selectedDateTime.isAfter(december31)) {
                        varn_December31.style.display = "block";

                        return false;
                    }
                }

                this.$store.dispatch('orderComponents/nextStepAction');
            });

            if (typeof dataLayer !== 'undefined' && typeof dataLayerProducts !== 'undefined' && dataLayerProducts && typeof deliveryTypeOrder !== 'undefined' && deliveryTypeOrder) {
                let option = deliveryTypeOrder === 'pvz' ? 'self' : 'courier';

                dataLayer.push({ecommerce: null});

                dataLayer.push({
                    event: 'checkout',
                    ecommerce: {
                        currencyCode: 'RUB',
                        checkout: {
                            actionField: {'step': 4, 'action': 'delivery', option: option},
                            products: dataLayerProducts
                        }
                    }
                });
            }

        },
        updated() {
            this.selected_DATE = this.PROPERTY_DELIVERY_DATE;

            this.$store.dispatch('orderProperties/orderCalculate');

            forEach(this.$el.querySelectorAll('.pane'), pane);
        }
    }
</script>