<template lang="pug">
  include ../../../../node_modules/bemto.pug/bemto
  include ../../../layouts/mixins
  div(:class="{'is-loading': !productData}")
    div(v-if="productData" :class="{'is-loading': loading}")
      h1 {{productData.set['name']}}
      +b.product-art
        +e.avail(v-if="productData.set['available']")  
        +e.avail(v-else="productData.set.available")   
        +e.articul  {{productData.set['articul']}}

      include ../../../components/page-product/productTop
      +productTop({
        source: 'vue'
      })
</template>

<script>
  import axios from "axios";
  import {forEach} from "../../../js/helpers";
  import {initSlider} from "../../../components/page-product/initSlider";
  import AppFastOrder from "../AppFastOrder.vue";
  import {add2Basket} from "../../../js/common/add2basket";

  export default {
    // props: ['sectionId', 'setId'],
    data () {
      return {
        loading: false,
        productData: '',
      }
    },
    components: {
      AppFastOrder
    },
    filters: {
      plusMinus(value) {
        if (!value) return '';
        const sign = value > 0 ? '+' : '';
        return sign + value
      }
    },
    methods: {
      getData(sectionId, setId) {
        let loading = true;
        axios.get('/api/v1/catalog/product/set/', {
          params: {
            sectionId,
            setId
          }
        })
          .then(response => {
            if (!response.data.success) throw response.data.errors[0].message;
            this.productData = JSON.parse(JSON.stringify(response.data.data));

            console.log('this.productData', this.productData);

            history.pushState(null, document.title, this.productData.set['url']);

            _tmr.push({ id: 3208143, type: "reachGoal", goal: "viewcontent", value: this.productData.set['price'].price });
            _tmr.push({type: 'itemView', productid: this.productData.set['id'], pagetype: 'product',  list: 1, totalvalue: this.productData.set['price'].price});

              if (typeof fbq !== 'undefined') {
                  fbq('track', 'ViewContent', {
                      content_ids: this.productData.set['id'],
                      content_name: this.productData.set['name'],
                      content_category: this.productData.set['sections_text_chain'],
                      value: this.productData.set['price'].price,
                      content_type: 'product',
                      currency: 'RUB',
                  });
              }

              if (typeof dataLayer !== 'undefined') {
                  dataLayer.push({ecommerce: null});
                  dataLayer.push({
                      event: 'showDetail',
                      ecommerce: {
                          currencyCode: 'RUB',
                          detail: {
                              products: [{
                                  name: this.productData.set['name'],
                                  id: this.productData.set['id'],
                                  price: this.productData.set['price'].price,
                                  category: this.productData.set['sections_text_chain'],
                              }]
                          }
                      }
                  });
              }

            loading = false;
          })
          .catch(function (error) {
            loading = false;
            console.log(error);
          });
      },
      changeItem(set_id) {
        const productSliderBig = this.$el.querySelector('.productSliderBig');
        const productSliderThumbs = this.$el.querySelector('.productSliderThumbs');
        if (productSliderBig && productSliderBig.swiper) {
          productSliderBig.swiper.destroy();
        }
        if (productSliderThumbs && productSliderThumbs.swiper) {
          productSliderThumbs.swiper.destroy();
        }
        this.getData(this.productData.set['section_id'], set_id);
      },
      add2BasketMethod() {
          _tmr.push({ id: 3208143, type: "reachGoal", goal: "cart", value: this.productData.set['price'].price });
          _tmr.push({type: 'itemView', productid: this.productData.set['id'], pagetype: 'cart',  list: 1, totalvalue: this.productData.set['price'].price});

          if (typeof fbq !== 'undefined') {
              fbq('track', 'AddToCart', {
                  content_ids: this.productData.set['id'],
                  content_name: this.productData.set['name'],
                  content_category: this.productData.set['sections_text_chain'],
                  value: this.productData.set['price'].price,
                  content_type: 'product',
                  currency: 'RUB',
              });
          }

          if (typeof dataLayer !== 'undefined') {
              dataLayer.push({ecommerce: null});
              dataLayer.push({
                  event: 'addToCart',
                  ecommerce: {
                      currencyCode: 'RUB',
                      add: {
                          actionField: 'categories',
                          products: [{
                              name: this.productData.set['name'],
                              id: this.productData.set['id'],
                              price: this.productData.set['price'].price,
                              category: this.productData.set['sections_text_chain'],
                              quantity: 1
                          }]
                      }
                  }
              });
          }

          new add2Basket(this.$el.querySelector('.js-add-to-basket')).init();
      }
    },
    created() {
      const $el = document.querySelector('.js-product-data');
      this.getData($el.getAttribute('data-sectionId'), $el.getAttribute('data-setId'));
    },
    updated() {
      this.$nextTick(() => {
        forEach(document.querySelectorAll('.productTop'), $container => initSlider($container));
      })
    },
  }
</script>

