import Vue from 'vue';
import AppOrder from './components/AppOrder.vue';
import AppProduct from './components/AppProduct.vue';
import store from './store'

Vue.config.productionTip = false;

const appOrderInstance = new Vue({
  render: h => h(AppOrder),
  store
});
const appOrderEl = document.querySelector('#app-order');
if (appOrderEl) {
  appOrderInstance.$mount('#app-order');
}

const appProductInstance = new Vue({
  render: h => h(AppProduct),
  store
});
const $appProduct = document.querySelector('#app-product');
if ($appProduct) {
  appProductInstance.$mount($appProduct);
}

import {forEach} from "../js/helpers";
forEach(document.querySelectorAll('.js-one-click-buy'), $button => {
  $button.addEventListener('click', () => {
    const $appFastOrder = document.querySelector('#app-fast-order');
    const AppFastOrder = () => import("./components/AppFastOrder.vue");
    const appFastOrderInstance = new Vue({
      render: h => h(AppFastOrder),
      store,
    });
    appFastOrderInstance.$mount($appFastOrder);
  });
});
