<template>
  <div v-if="littleThingsProducts.length" class="sliderMini sliderMini_orderSide">
    <div class="sliderMini__header">
      <div class="sliderMini__title">    </div>
      <div class="sliderMini__nav">
        <div class="swiper-navigation">
          <div class="swiper-button-prev"></div>
          <div class="swiper-button-next"></div>
        </div>
      </div>
    </div>
    <div class="sliderMini__slider swiper-container">
      <div class="swiper-wrapper">
        <div v-for="product in littleThingsProducts" class="swiper-slide" :data-id="product.ID">
          <div class="sliderMiniItem">
            <a class="sliderMiniItem__img" :href="product.URL">
              <!--suppress HtmlUnknownTarget -->
              <img :alt="product.NAME" :src="product['IMAGE']['small_src']">
            </a>
            <a target="_blank" class="sliderMiniItem__title" :href="product.URL">{{product.NAME}}</a>
            <div class="relative">
              <div class="sliderMiniItem__submit js-add-to-basket link" @click="clickButton" data-popover="size: sm" :data-id="product.ID">+ {{product["PRICES"]["price_discount_format"]}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {sliderMini} from "../../../components/sliderMini/sliderMini";
  import {add2Basket} from "../../../js/common/add2basket";
  import {mapState} from "vuex";

  export default {
    computed: {
      ...mapState('basket' ,[
        'littleThingsProducts',
      ]),
    },
    methods: {
      events() {
        document.addEventListener('product-added', e => {
          console.log(e);
          this.$emit('get-basket');
          e.detail.popoverEl.addEventListener('hidden', () => {
            this.$store.dispatch('basket/getLittleThings');
          });
        })
      },
      clickButton(e) {
        new add2Basket(e.target).getData();
      }
    },
    mounted() {
      this.events();
      this.$store.dispatch('basket/getLittleThings');
    },
    updated() {
      this.$nextTick(() => {
        console.log('updated');
        if(this.littleThingsProducts.length === 0) return;
        sliderMini(this.$el);
      })
    }
  }
</script>
