<template>
  <div class="promoCode form-group">
    <label for="coupon" class="promoCode__title"> </label>
    <div class="promoCode__control">
      <input v-model="inputCoupon" @input="inputCouponFunc" :data-state="couponState" id="coupon" class="form-control"
             type="text">
      <div v-if="errorText" class="help-text error-message">{{errorText}}</div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";
  import _ from "lodash";
  import {mapState} from "vuex";

  export default {
    data() {
      return {
        inputCoupon: '',
        acceptedCoupon: '',
        couponState: '',
        errorText: '',
      }
    },
    computed: {
      ...mapState('orderProperties', [
        'DELIVERY_ID'
      ]),
    },
    methods: {
      inputCouponFunc() {
        this.errorText = '';
        this.couponState = '';
        if (this.inputCoupon.length > 3) {
          this.debouncedSetCoupon();
        } else if(this.acceptedCoupon) {
          this.deleteCoupon();
        }
      },
      getCoupon() {
        axios.get('/api/v1/sale/order/coupon/')
          .then(response => {
            console.log(response);
            if (!response.data.success) throw response.data.errors[0].message;

            const {COUPON} = response.data.data;
            if (COUPON.length) {
              this.acceptedCoupon = COUPON[0].COUPON;
              this.couponState = 'success';
            }
          })
          .catch(error => {
            console.log(error);
          });
      },
      setCoupon() {
        this.couponState = 'loading';
        axios.post('/api/v1/sale/order/coupon/', {
          COUPON: this.inputCoupon
        })
          .then(response => {
            console.log(response);
            if (!response.data.success) throw response.data.errors[0].message;

            const {COUPON} = response.data.data;
            if (COUPON.length) {
              this.couponState = 'success';
              this.acceptedCoupon = COUPON[0].COUPON;
              this.updatePrices();
            }
          })
          .catch(error => {
            console.log(error);
            this.couponState = 'error';
            this.errorText = error;
          });
      },
      deleteCoupon() {
        this.couponState = 'loading';
        axios.delete(`/api/v1/sale/order/coupon/${this.acceptedCoupon}/`)
          .then(response => {
            console.log(response);
            if (!response.data.success) throw response.data.errors[0].message;

            this.couponState = '';
            this.acceptedCoupon = '';
            this.updatePrices();
          })
          .catch(error => {
            console.log(error);
            this.couponState = 'error';
            this.errorText = error;
          });
      },
      updatePrices() {
        this.$emit('get-basket');
        this.$store.dispatch('orderProperties/orderCalculate');
      }
    },
    created() {
      this.getCoupon();
    },
    mounted() {
      this.debouncedSetCoupon = _.debounce(this.setCoupon, 1500);
    },
    watch: {
      acceptedCoupon() {
        this.inputCoupon = this.acceptedCoupon;
      },
    }
  }
</script>

