    <!--              <use xlink:href="#apple-pay"></use>-->
    <!--            </svg></div>-->
    <!--            <div class="b-pay__item"><svg width="82" height="33">-->
    <!--              <use xlink:href="#google-pay"></use>-->
    <!--            </svg></div>-->
    <!--          </div>-->
    <!--        </div>-->
    <!--      </div>-->
    <!--      <div class="cart-order-grid__item"></div>-->
    <!--    </div>-->
    <div class="cart-order-grid">
      <div class="cart-order-grid__item">
        <div class="form-group form-group_lg">
          <div class="radio-toggle">
            <div v-for="pay in PAY_SYSTEM" :key="pay.ID" class="radio-toggle__item">
              <!--suppress JSUnresolvedVariable, HtmlFormInputWithoutLabel -->
              <input v-model="PAY_SYSTEM_ID" :value="pay.ID" class="checkbox" type="radio"
                     :id="`cart-5-radio-pay-${pay.ID}`" name="cart-5-radio-pay"
                     :disabled="ORDER_ID !== null">
              <label :for="`cart-5-radio-pay-${pay.ID}`">{{pay.NAME}}</label>
              <div class="radio-toggle__content">
                <!--suppress JSUnresolvedVariable -->
                <template v-if="PAY_SYSTEM_ID === pay.ID && PAY_SYSTEM_ID === 3">
                  <div class="muted">      </div>
                </template>
                <!--suppress JSUnresolvedVariable -->
                <template v-if="PAY_SYSTEM_ID === pay.ID && PAY_SYSTEM_ID === 4">
                  <div class="form-group">
                    <label for="company-name" class="sr-only"></label>
                    <!--suppress JSUnresolvedVariable -->
                    <input v-model="PROPERTY_COMPANY_NAME" id="company-name" class="form-control form-control_sm"
                           placeholder=" " type="text" required>
                  </div>
                  <div class="file-wrapper">
                    <input type="file" size="5242880" required>
                    <svg width="39" height="39">
                      <use xlink:href="#clip"></use>
                    </svg>
                    <div class="file-wrapper__content">
                        
                      <div class="text-muted">    5 .</div>
                    </div>
                  </div>
                </template>
              </div>
              <div v-if="pay.ID === 2" class="radio-toggle__content">
                <!--                <input class="form-control form-control_sm" placeholder="  " type="text">-->
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="cart-order-grid__item">
        <div class="form-group">
          <!--suppress JSUnresolvedVariable, HtmlFormInputWithoutLabel -->
          <textarea v-model="USER_DESCRIPTION" class="form-control" placeholder="  "></textarea>
        </div>
      </div>
    </div>
    <div class="cart-order-grid">
      <div class="cart-order-grid__item">
        <button class="button button-dark button-block button-submit" id="cart_pay_button">
          <!--suppress JSUnresolvedVariable -->
          <template v-if="PAY_SYSTEM_ID === 3"> {{Number((TOTAL['ORDER_PRICE'] - BONUS_PAY)).toLocaleString('fr-FR')}} </template>
          <template v-else> </template>
        </button>
      </div>
      <div class="cart-order-grid__item">
        <div class="cart-order-ofert">
          <div><span>   , &nbsp;  </span><a href="#"> </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import {mapFields} from "vuex-map-fields";
    import {mapState} from "vuex";
    import validate from "../../mixins/validate";
    import axios from "axios";
    import {File} from "../../../components/file/File";
    import FormData from 'form-data';

    export default {
        mixins: [validate],
        props: [
            'stepIndex',
        ],
        data() {
            return {
                PAY_SYSTEM: []
            }
        },
        computed: {
            ...mapFields('orderProperties', [
                'PERSON_TYPE_ID',
                'USER_DESCRIPTION',
                'PAY_SYSTEM_ID',
                'PROPERTY_COMPANY_NAME'
            ]),
            ...mapState('orderProperties', [
                'PROPERTY_EMAIL',
                'PROPERTY_PERSONAL_PHONE',
                'DELIVERY_ID',
                'ORDER_ID',
                'ORDER_PRODUCTS',
                'TOTAL',
                'BONUS_PAY',
                'LOCATION'
            ]),
        },
        methods: {
            getPaySystem() {
                axios.get('/api/v1/sale/order/paysystem/', {
                    params: {
                        LOCATION: this.LOCATION,
                        PERSON_TYPE_ID: this.PERSON_TYPE_ID,
                        DELIVERY_ID: this.DELIVERY_ID
                    }
                })
                    .then(response => {
                        if (!response.data.success) throw response.data.errors[0].message;
                        this.PAY_SYSTEM = response.data.data.PAY_SYSTEM;
                    })
                    .catch(error => {
                        console.log(error);
                    });
            },
            pay() {
                // noinspection JSUnresolvedVariable,JSUnresolvedFunction
                const widget = new cp.CloudPayments();
                const self = this;

                let items = [];

                self.ORDER_PRODUCTS.forEach(function (product) {
                    items.push({
                        label: product.NAME,
                        price: product.PRICE,
                        quantity: product.QUANTITY,
                        amount: product.PRICE * product.QUANTITY,
                        vat: null
                    });
                });

                if(self.TOTAL['DELIVERY_PRICE']){
                    items.push({
                        label: '',
                        price: self.TOTAL['DELIVERY_PRICE'],
                        quantity: 1,
                        amount:self.TOTAL['DELIVERY_PRICE'],
                        vat: null
                    });
                }

                let phone = self.PROPERTY_PERSONAL_PHONE.replace(new RegExp('\\D', 'g'), '');

                // noinspection JSUnresolvedFunction
                widget.charge({ // options
                        publicId: 'pk_038d41d7a83fae1c4b1fdcb8d7650',  //id   
                        description: '  ' + self.ORDER_ID, //
                        amount: self.TOTAL['ORDER_PRICE'] - self.BONUS_PAY, //
                        currency: 'RUB', //
                        invoiceId: self.ORDER_ID, //   ()
                        accountId: self.PROPERTY_EMAIL, //  ()
                        skin: "mini", // 
                        data: {
                            cloudPayments:{
                                CustomerReceipt: {
                                    Items: items,
                                    amounts: {
                                        electronic: self.TOTAL['ORDER_PRICE'] - self.BONUS_PAY,
                                    },
                                    taxationSystem: 1,
                                    email: self.PROPERTY_EMAIL,
                                    phone: phone,
                                }
                            },
                        }
                    },
                    () => { // success
                        this.$store.dispatch('orderProperties/goToSuccessPage');
                    },
                    error => { // fail
                        location.href = location.origin + '/personal/order/make/?ORDER_ID=' + self.ORDER_ID + '&ERROR=' + error;
                    });
            },
            setFile(input) {
                let formData = new FormData();
                if (input.files.length) {
                    let file = input.files[0];
                    formData.append('file', file);

                    // You should have a server side REST API
                    axios.post('/api/v1/file/upload/',
                        formData, {
                            headers: {
                                'Content-Type': 'multipart/form-data'
                            }
                        }
                    ).then(response => {
                        if (!response.data.success) throw response.data.errors[0].message;
                        this.$store.commit('setState', {
                            module: 'orderProperties',
                            state: 'FILE_BILL',
                            value: response.data.data.id
                        });
                    })
                        .catch(error => {
                            console.log(error);
                        });
                } else {
                    this.clearCompanyData()
                }
            },
            clearCompanyData() {
                if (this.uploadFile) {
                    this.uploadFile = undefined;
                }
                this.$store.commit('orderProperties/clearCompanyData');
            }
        },
        mounted() {
            this.getPaySystem();
            this.form.addEventListener('bouncerFormValid', () => {
                this.$store.dispatch('orderProperties/orderSave');
            });

            if (typeof dataLayer !== 'undefined' && typeof dataLayerProducts !== 'undefined' && dataLayerProducts){
                dataLayer.push({ ecommerce: null });

                dataLayer.push({
                    event: 'checkout',
                    ecommerce: {
                        currencyCode: 'RUB',
                        checkout: {
                            actionField: {'step': 5, 'action': 'time'},
                            products: dataLayerProducts
                        }
                    }
                });
            }
        },
        updated() {
            this.$nextTick(() => {
                console.log('updated');
                if (this.PAY_SYSTEM_ID === 4) {
                    if (this.uploadFile) return;
                    this.uploadFile = new File(this.$el.querySelector('.file-wrapper'));
                    this.uploadFile.init(this.setFile);
                } else {
                    this.clearCompanyData();
                }
            })
        },
        watch: {
            ORDER_ID() {
                switch (this.PAY_SYSTEM_ID) {
                    case 2:
                        if (typeof ym !== 'undefined')
                            ym(71116915, 'reachGoal', 'cashOrder');
                        this.$store.dispatch('orderProperties/goToSuccessPage');
                        break;
                    case 3:
                        if (typeof ym !== 'undefined')
                            ym(71116915, 'reachGoal', 'onlineOrder');
                        this.pay();
                        break;
                    case 4:
                        this.$store.dispatch('orderProperties/goToSuccessPage');
                        break;
                }
            }
        }
    }
</script>
