<template>
  <div v-if="BASKET_ITEMS" class="itemSmList">
    <div class="itemSm" v-for="item in BASKET_ITEMS" :key="item.ID">
      <div class="itemSm__close" v-on:click="removeItem(item.ID)"></div>
      <!--suppress JSUnresolvedVariable -->
      <a class="itemSm__img" :href="item.DETAIL_PAGE_URL">
        <!--suppress JSUnresolvedVariable, HtmlUnknownTarget -->
        <img :alt="item.PRODUCT_DATA.name" :src="item.PRODUCT_DATA.image.small_src">
      </a>
      <div class="itemSm__content">
        <!--suppress JSUnresolvedVariable -->
        <a class="itemSm__title" v-bind:href="item.DETAIL_PAGE_URL">{{item.PRODUCT_DATA.name}}</a>
        <div class="itemSm__avail"> </div>
        <div class="itemSm__sizes">
          <div v-if="item.PRODUCT_DATA.size" class="itemSm__size"> {{item.PRODUCT_DATA.size.name}}</div>
          <!--suppress JSUnresolvedVariable -->
          <div v-if="item.PRODUCT_DATA.packing" class="itemSm__size">{{item.PRODUCT_DATA.packing.name}}</div>
        </div>
      </div>
      <div class="itemSm__side">
        <div class="itemSm__cat">
          <!--suppress JSUnresolvedVariable -->
          <span v-for="berrie in item.PRODUCT_DATA.berries">{{berrie.name}}</span>
        </div>
        <div class="itemSm__counterPrice">
          <!--suppress JSUnresolvedVariable -->
          <button-counter v-bind:quantity="item.QUANTITY" v-bind:id="item.ID" v-on:delete-item="removeItem(item.ID)" v-on:update-totals="updateTotals"></button-counter>
          <div class="itemSm__priceWrapper">
            <!--suppress JSUnresolvedVariable -->
            <div class="itemSm__priceNew is-red">{{item.PRICE_FORMATTED}}</div>
            <!--suppress JSUnresolvedVariable -->
            <div v-if="item.DISCOUNT_PRICE" class="itemSm__priceOld">{{item.BASE_PRICE_FORMATTED}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";
  import ButtonCounter from "./ButtonCounter.vue";

  export default {
    props: ['BASKET_ITEMS'],
    methods: {
      updateItems: function (data) {
        const self = this;
        self.$emit('update-items', data);
      },
      updateTotals: function (data) {
        const self = this;
        self.$emit('update-totals', data);
      },
      removeItem: function (id) {
        console.log('removeItem ' + id);
        axios.delete(`/api/v1/sale/order/basket/item/${id}/?GET_PRODUCTS_DATA=N`)
          .then(response => {
            console.log(response);
            if (!response.data.success) throw response.data.errors[0].message;

            const {BASKET_TOTALS} = JSON.parse(JSON.stringify(response.data.data));
            this.$store.commit('basket/removeItem', id);
            this.updateTotals({BASKET_TOTALS});
            this.$store.dispatch('basket/getLittleThings');
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    },
    components: {
      ButtonCounter
    }
  }
</script>
