<template>
  <div>
    <div class="cart-order-grid">
      <div class="cart-order-grid__item">
        <div class="form-group form-group_sm">
          <input v-model="isClientRecipient" class="checkbox" type="checkbox" id="cart-2-checkbox-1" data-toggle="checkbox-toggle" data-target="#cart-2-content-1" data-swap="true">
          <label for="cart-2-checkbox-1"> </label>
        </div>
        <transition name="fade">
          <div v-if="!isClientRecipient">
            <div class="form-group form-group_lg form-group_name-take">
              <label for="cart-2-checkbox-9"> </label>
              <!--suppress JSUnresolvedVariable -->
              <input id="cart-2-checkbox-9" v-model="PROPERTY_RECIPIENT_NAME" class="form-control" type="text" placeholder=" " required>
            </div>
            <div class="form-group form-group_sm">
              <input v-model="isRecipientDontKnow" class="checkbox" type="checkbox" id="cart-2-checkbox-2" data-toggle="checkbox-toggle" data-target="#cart-2-content-2" data-swap="true">
              <label for="cart-2-checkbox-2">   </label>
            </div>
            <transition name="fade">
              <div v-if="!isRecipientDontKnow">
                <div class="form-group form-group_lg">
                  <label for="cart-2-checkbox-8"> </label>
                  <!--suppress JSUnresolvedVariable -->
                  <input id="cart-2-checkbox-8" v-model="PROPERTY_RECIPIENT_PHONE" class="form-control" type="tel" data-field="phone" placeholder="  " minlength="11" required />
                </div>
              </div>
            </transition>
          </div>
        </transition>
        <div class="form-group checkbox-group">
          <!--suppress JSUnresolvedVariable -->
          <input v-model="PROPERTY_ANONYMOUS_ORDER" true-value="Y" false-value="N" class="checkbox" type="checkbox" id="cart-2-checkbox-5">
          <label for="cart-2-checkbox-5"> <span class="quest quest_gray"><span class="quest__title"></span><span class="quest__content"> </span></span></label>
        </div>
      </div>
      <div class="cart-order-grid__item cart-order-grid__item_swap">
        <div class="form-group form-group_sm">
          <input v-model="PROPERTY_NEED_POSTCARD" true-value="Y" false-value="N" class="checkbox checkbox_toggle" type="checkbox" id="cart-2-checkbox-6" data-toggle="checkbox-toggle" data-target="#cart-2-content-6">
          <label for="cart-2-checkbox-6"> </label>
        </div>
        <transition name="fade">
          <div v-if="PROPERTY_NEED_POSTCARD === 'Y'">
            <div class="form-group form-group_lg">
              <label for="PROPERTY_POSTCARD"> :</label>
              <!--suppress JSUnresolvedVariable -->
              <textarea id="PROPERTY_POSTCARD" v-model="PROPERTY_POSTCARD"
                        class="form-control form-control-order-mail-text"
                        placeholder="  ,    ,       "></textarea>
            </div>
          </div>
        </transition>
      </div>
    </div>
    <div class="cart-order-grid">
      <div class="cart-order-grid__item"><button type="submit" class="button button-dark button-block button-submit"></button></div>
      <div class="cart-order-grid__item"></div>
    </div>
  </div>
</template>

<script>
  import {mapFields} from "vuex-map-fields";
  import validate from "../../mixins/validate";
  import {phoneMask} from "../../../js/phone-mask";

  export default {
    mixins: [validate],
    props: [
      'stepIndex'
    ],
    data () {
      return {
        isClientRecipient: false,
        isRecipientDontKnow: false,
      }
    },
    computed: {
      ...mapFields('orderProperties', [
        'PROPERTY_RECIPIENT_NAME',
        'PROPERTY_RECIPIENT_PHONE',
        'PROPERTY_RECIPIENT_MAKE_PHOTO',
        'PROPERTY_IS_SURPRISE',
        'PROPERTY_ANONYMOUS_ORDER',
        'PROPERTY_NEED_POSTCARD',
        'PROPERTY_POSTCARD'
      ]),
    },
    mounted() {
      phoneMask(this.$el.querySelector('[data-field="phone"]'));
      this.form.addEventListener('bouncerFormValid', () => {
        this.$store.dispatch('orderComponents/nextStepAction');
      });

        if (typeof dataLayer !== 'undefined' && typeof dataLayerProducts !== 'undefined' && dataLayerProducts){
            dataLayer.push({ ecommerce: null });

            dataLayer.push({
                event: 'checkout',
                ecommerce: {
                    currencyCode: 'RUB',
                    checkout: {
                        actionField: {'step': 2, 'action': 'autoriz'},
                        products: dataLayerProducts
                    }
                }
            });
        }
    }
  }
</script>
