<template>
  <div>


    <div class="box-wrap">


      <div class="form-group form-group_line_inline cart-order__box"
           :class="{'cart-order__box_static': isPROPERTY_GET_ADDRESS}">
        <div v-for="(delivery, key) in deliveryVariants">
          <input id="deliveryType" v-model="deliveryType" :value="key" name="delivery"
                 class="checkbox" type="radio" :id="'cart-3-radio-'+key">
          <label for="deliveryType" :for="'cart-3-radio-'+key">{{ delivery.TITLE }}</label>
        </div>
      </div>

      <div class="order-address-shop">
        <div class="order-address-shop-shop__title"> </div>

        <select class="select js-deliv-change-city test choices__input" v-on:change="changeLocation($event)">
          <template v-for="(stringPvzAddressListVariant, key) in stringPvzAddressList">
            <option v-if="stringPvzAddressListVariant.VALUE == addrId" :value="stringPvzAddressListVariant.VALUE"
                    selected>{{ stringPvzAddressListVariant.TITLE }}
            </option>
            <option v-else="" :value="stringPvzAddressListVariant.VALUE">{{
                stringPvzAddressListVariant.TITLE
              }}
            </option>
          </template>
        </select>
      </div>

    </div>


    <transition name="fade">
      <div v-if="deliveryType === 'courier'">
        <template v-if="!isPROPERTY_GET_ADDRESS">
          <div class="order-courier-distance">
            <div v-on:click="isShowDistanceMap = !isShowDistanceMap"
                 class="button button-border-gray button-courier-map text-uppercase"> 
            </div>

            <div v-if="PROPERTY_DISTANCE" class="order-courier-distance__text"> 
              {{ ringRoadName }}<span>{{ PROPERTY_DISTANCE }} </span>
            </div>
          </div>
          <div v-show="isShowDistanceMap" class="orderDistanceMap" :class="{'is-active': isShowDistanceMap}"></div>
        </template>
        <div class="form-group form-group_sm">
          <!--suppress JSUnresolvedVariable -->
          <input v-model="PROPERTY_GET_ADDRESS" true-value="Y" false-value="N" id="cart-3-checkbox-1" class="checkbox"
                 type="checkbox" data-swap="true">
          <label for="cart-3-checkbox-1">   </label>
        </div>
        <transition name="fade">
          <div v-if="!isFilled && !isPROPERTY_GET_ADDRESS">
            <div class="cart-order-grid">
              <div class="cart-order-grid__item">
                <div class="form-group form-group_sm">
                  <label for="PROPERTY_CITY"></label>
                  <!--suppress JSUnresolvedVariable -->
                  <input id="PROPERTY_CITY" v-model="PROPERTY_CITY" @input="inputAddress" :data-state="cityState"
                         name="CITY" class="form-control" type="text"
                         placeholder=" " disabled required>
                </div>
                <div class="form-group form-group_sm">
                  <label for="PROPERTY_STREET"></label>
                  <!--suppress JSUnresolvedVariable -->
                  <input id="PROPERTY_STREET" v-model="PROPERTY_STREET" @input="inputAddress" class="form-control"
                         type="text"
                         placeholder=" " required>
                </div>
                <div class="form-group form-group_half">
                  <div>
                    <label for="PROPERTY_HOME">/</label>
                    <!--suppress JSUnresolvedVariable -->
                    <input id="PROPERTY_HOME" v-model="PROPERTY_HOME" @input="inputAddress" class="form-control"
                           type="text"
                           placeholder="" required>
                  </div>
                  <div>
                    <label for="PROPERTY_APARTMENT">/</label>
                    <!--suppress JSUnresolvedVariable -->
                    <input id="PROPERTY_APARTMENT" v-model="PROPERTY_APARTMENT" class="form-control" type="text"
                           placeholder="">
                  </div>
                </div>
              </div>
              <div class="cart-order-grid__item">
                <div data-move="order-sum-set"></div>
                <div class="cart-order__comment form-group">
                  <label for="PROPERTY_DELIVERY_COMMENT">  :</label>
                  <!--suppress JSUnresolvedVariable -->
                  <textarea id="PROPERTY_DELIVERY_COMMENT" v-model="PROPERTY_DELIVERY_COMMENT" class="form-control"
                            placeholder=" "></textarea>
                </div>
              </div>
            </div>
          </div>
        </transition>
        <div class="cart-order-grid">
          <div class="cart-order-grid__item">
            <button type="submit" :disabled="cityState === 'loading'"
                    class="button button-dark button-block button-submit">
            </button>
          </div>
          <div class="cart-order-grid__item cart-order-grid__item_filled-swap">
            <div v-if="DELIVERY_PRICE_FORMATED" data-move="order-sum-get">
              <div class="order-sum">
                <div class="order-sum__title">
                   
                  <div class="quest quest_gray">
                    <div class="quest__title"></div>
                    <div class="quest__content"> </div>
                  </div>
                </div>
                <div class="order-sum__price">{{ DELIVERY_PRICE_FORMATED }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="fade">
      <div v-if="deliveryType === 'pvz'">
        <div class="order-address">
          <div class="order-address__title"> </div>
          <div class="order-address__text">{{ stringPvzAddress }}</div>
        </div>
        <div class="orderMap" id="pickupMap" data-lat="55.817405" data-lng="37.593147" data-icon="iconPoint"
             data-icon-size="48 61"></div>
        <div class="cart-order-grid">
          <div class="cart-order-grid__item">
            <button type="submit" class="button button-dark button-block button-submit"></button>
          </div>
          <div class="cart-order-grid__item cart-order-grid__item_swap workTime workTime_cart-order">
            <div class="workTime__title"> </div>
            <div class="workTime__list" v-html="deliveryVariants.pvz.ROWS[0].SCHEDULE"></div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import _ from 'lodash';
import {map} from "../../../components/map/map";
import {DistanceMap} from "../../../components/cart-order/DistanceMap";
import {mapFields} from "vuex-map-fields";
import {mapState} from "vuex";
import validate from "../../mixins/validate";
import Bouncer from "../../../js/bouncer";
import {validateConfig} from "../../../js/validateConfig";
import {changeCity} from "../../../js/common/changeCity";
import {forEach} from "../../../js/helpers";
import {add2Basket} from "../../../js/common/add2basket";

export default {
  mixins: [validate],
  props: [
    'stepIndex',
    'isFilled'
  ],
  data() {
    return {
      isAddressFromMap: false,
      isShowDistanceMap: true,
      city: document.querySelector('.js-change-city').selectedOptions[0].text,
      cityState: '',
      addrId: document.querySelector('.js-change-city').selectedOptions[0].value
    }
  },
  computed: {
    ...mapState('orderProperties', [
      'PROPERTY_DISTANCE',
      'DELIVERY_PRICE_FORMATED'
    ]),
    ...mapState('orderComponents', [
      'deliveryVariants',
      'deliveryType'
    ]),
    ...mapFields('orderProperties', [
      'PROPERTY_GET_ADDRESS',
      'LOCATION',
      'PROPERTY_CITY',
      'PROPERTY_STREET',
      'PROPERTY_HOME',
      'PROPERTY_APARTMENT',
      'PROPERTY_DELIVERY_COMMENT',
    ]),
    ...mapFields('orderComponents', [
      'deliveryType'
    ]),
    isPROPERTY_GET_ADDRESS() {
      return this.PROPERTY_GET_ADDRESS === 'Y'
    },
    ringRoadName() {
      let name = '';
      switch (this.city) {
        case '':
          name = '';
          break;
        case '-':
          name = '';
          break;
      }
      return name
    },
    stringPvzAddress() {
      if (!this.deliveryVariants) return;
      return this.deliveryVariants.pvz.ROWS[0].ADDRESS
    },
    stringPvzAddressList() {
      let addrAr = [
        {TITLE: '', VALUE: '0c5b2444-70a0-4932-980c-b4dc0d3f02b5'},
        {TITLE: '-', VALUE: 'c2deb16a-0330-4f05-821f-1d09c93331e6'},
        //{TITLE: '', VALUE: 'd7a10054-da31-4560-8121-7c2f33092883'},
      ];

      return addrAr;
    },
    coordinates() {
      if (!this.deliveryVariants) return;
      const coordinates = [];
      coordinates.push(+this.deliveryVariants.pvz.ROWS[0].GPS_N, +this.deliveryVariants.pvz.ROWS[0].GPS_S);
      return coordinates
    }
  },
  mounted() {
    this.debouncedGeoCodeAddress = _.debounce(this.geoCodeAddress, 2000);
    this.$store.dispatch('orderComponents/getAvailableDelivery');

    document.addEventListener('order-address', e => {
      this.isAddressFromMap = true;
      setTimeout(() => {
        this.isAddressFromMap = false
      }, 2500);
      const address = e.detail.addressToDetailed;
      this.$store.commit('orderProperties/updateAddress', {
        distance: e.detail.routeLength,
        city: address.description.replace(', ', ''),
        street: address.name.split(',')[0].replace('', ''),
        home: address.name.split(',')[1]
      });
      this.$store.dispatch('orderProperties/orderCalculate');
    });
    document.addEventListener('order-address-error', e => {
      this.cityState = '';
      this.validateCity(e);
    });
    document.addEventListener('order-address-city-ok', () => {
      this.cityState = '';
    });
    this.form.addEventListener('bouncerFormValid', () => {
      this.cityState = '';
      this.$store.dispatch('orderProperties/orderCalculate');
      this.$store.dispatch('orderComponents/nextStepAction');
    });

      if (typeof dataLayer !== 'undefined' && typeof dataLayerProducts !== 'undefined' && dataLayerProducts){
          dataLayer.push({ ecommerce: null });

          dataLayer.push({
              event: 'checkout',
              ecommerce: {
                  currencyCode: 'RUB',
                  checkout: {
                      actionField: {'step': 3, 'action': 'recipientt'},
                      products: dataLayerProducts
                  }
              }
          });
      }
  },
  watch: {
    deliveryType() {
      this.$store.commit('setState', {
        module: 'orderProperties',
        state: 'DELIVERY_ID',
        value: this.deliveryVariants[this.deliveryType].ROWS[0].ID
      });
      switch (this.deliveryType) {
        case 'courier':
          setTimeout(() => {
            this.initCourierMap();
          }, 1000);
          break;
        case 'pvz':
          this.isShowDistanceMap = false;
          this.initPvzMap();
          break;
      }
    },
    PROPERTY_GET_ADDRESS(value) {
      if (value === 'Y') {
        this.$store.commit('orderProperties/clearAddress');
        this.$store.dispatch('orderProperties/orderCalculate');
      }
    }
  },
  methods: {
    initCourierMap() {
      const map = this.$el.querySelector('.orderDistanceMap');
      if (!map) return;
      map.classList.add('is-active');
      // todo      setTimeout
      setTimeout(() => {
        new DistanceMap({
          element: this.$el.querySelector('.orderDistanceMap'),
          CITY: this.PROPERTY_CITY,
          LOCATION: this.LOCATION,
          coordinates: this.coordinates,
          addressFrom: this.stringPvzAddress
        }).init();
      }, 1000);
    },
    initPvzMap() {
      // todo      setTimeout
      setTimeout(() => {
        map('pickupMap', this.coordinates);
      }, 1000);
    },
    inputAddress(e) {
      this.debouncedGeoCodeAddress(e)
    },
    geoCodeAddress(e) {
      if (this.PROPERTY_CITY.length < 5) return;
      if (e.target.name === 'CITY') {
        this.cityState = 'loading';
      }
      if (!this.isAddressFromMap) {
        document.dispatchEvent(new CustomEvent("order-delivery-geocode", {'detail': [this.PROPERTY_CITY, this.PROPERTY_STREET, this.PROPERTY_HOME]}));
      }
    },
    validateCity(e) {
      const field = this.$el.querySelector(`[name=CITY`);
      if (e.detail.value) {
        this.PROPERTY_CITY = e.detail.value;
      }
      field.setAttribute('data-bouncer-delivery-not-possible', e.detail.value);
      new Bouncer().validate(field, validateConfig);
    },
    changeLocation(e) {
      let search = e.target.value;
	  let mapbox = this.$el.querySelector('.orderDistanceMap');
	  mapbox.classList.remove("inited");
	  mapbox.innerHTML = "";
	  this.PROPERTY_CITY = e.target.options[e.target.selectedIndex].text;
	  this.CITY = e.target.options[e.target.selectedIndex].text;
	  this.LOCATION = e.target.value;
	  this.$store.dispatch('orderComponents/getAvailableDelivery');
	  this.initCourierMap();
    },
  }
}
</script>
